export default {
  name: "Intro",
  head () {
    return {
      title: 'Amanda Graham - Praana Wellness',
    }
  },
  async asyncData({ $prismic, error }) {
    try {
      const praana = (await $prismic.api.getSingle('praana')).data

      return {
        praana
      };
    } catch (e) {
      error({ statusCode: 404, message: "Page not found" });
    }
  }
}
